<script>
    import {log} from "./Store.js";
    console.log($log);
    export let logMessage ;
    let visible = false;
    
    function newMessage(message){
        $log = [...$log,message];
        visible = true;
    }
    $: newMessage(logMessage);
</script>
{#if visible}
<div id='log' on:click={()=>visible=false } on:keypress ={()=>visible=false}>
    {#each $log as line}
        <div>{line}</div>
    {/each}
</div>
{/if}

<style>
    #log {
        background-color: red;
        color: yellow;
        z-index: 100;
        display: block;
    }
</style>