<script>
    export let body;
    export let dates;
    export let workers;
    import Cell from "./Cell.svelte"
    import Header from "./Header.svelte"
    let week = dates.length>1?true:false; // false mean 1 day
</script>
{#if workers.length > 0 }
    <table>
        <!-- headers -->
        <thead>
            <tr>
                {#if week}
                    <Header header =  Shift />
                    <Header header =  Name />
                {:else}
                    <Header header =  Shift thClass="shift" />
                {/if}
            
                {#each dates as day}
                    {#if week}
                        <Header header = {day[0]} header2={day[1]} event = "{day[0]}"/>
                    {:else}
                        <Header header = {day[0]+" "+day[1]} event = "{day[0]}"/>
                    {/if}
                {/each}
            </tr>
        </thead>
        <tbody>
            {#each body as row,i}
                <tr>
                    <!-- shift number -->
                    
                    <Cell cell={i+1} tdClass="shift" />
                   
                    <!-- worker -->
                    {#if week}
                        <Cell cell={workers[i]} tdClass="worker" />
                    {/if}
                    
                    {#each row as cell}
                        {#if week}
                            <Cell cell={cell.shift} tdClass={cell.class} selectedDay={cell.selected} /> 
                        {:else}
                            <Cell cell={cell.shift} cell2={workers[i]} tdClass={cell.class} />
                        {/if}    
                    {/each}
                </tr>
            {:else}
            Empty list
            {/each}
        </tbody>
    </table>
{/if}

<style>
    table{
        /* font-family: "helvetica neue", helvetica, arial, sans-serif; */
        font-family: 'Palatino', 'Palatino Linotype', 'Book Antiqua', serif;
        table-layout: fixed;
        width: 100%;
        border-collapse: collapse;
        text-align: center;
        background-color: rgb(203, 252, 252);
        padding: 10px;
        border: 4px ridge rgb(187, 175, 240);
    }

    /* tr:nth-child(odd) {
        font-weight: bold;
    } */

</style>