<script>
    import {showDate} from "./DateStore";
    const next = ">>";
    const prev = "<<";
    const nextWeek = ">>>>>  ";
    const prevWeek = "  <<<<<";
</script>

<button on:click={()=>showDate.prevWeek()} class="left">{prevWeek}</button>
<button on:click={()=>showDate.prevDay()}>{prev}</button>
<button on:click={()=>showDate.nextDay()}>{next}</button>
<button on:click={()=>showDate.nextWeek()} class="right">{nextWeek}</button>

<style>

    button {
        border: solid 3px rgb(187, 175, 240);
        /* border-radius: 10px; */
        color: blue;
        background-color:antiquewhite; 
        padding: 3px;
        margin: 0px;
    }

    .left{
        border-top-left-radius: 50%;
        border-bottom-left-radius: 50%;
    }
    .right{
        border-top-right-radius: 50%;
        border-bottom-right-radius: 50%;
    }
</style>