<script>
    export let header = "";
    export let header2 = "";
    export let thClass = "";
    import {showDate} from "./Store";
    export let event = "";
    // $showDate;

    
    function changeDate(day){
        if (day !== "")
            $showDate = day;
    }

</script>

<th class={thClass} on:click={changeDate(event)}>
    {header}<br>{header2}
</th>

<style>
    th {

        /* opacity: 0.5; */
        border: 1px black solid;
        padding-top: 4px;
        padding-bottom: 4px;
        /* position: relative; */

        /* overflow: hidden; */
    }

  
    th:nth-child(1) {
         width: 5%;
         color: darkred;
    }
    th.shift{
        width: 15%;
    }
</style>