<script>
	export let workers; 
	export let shiftKey;
	export let shiftsWeek;

	import {swipe} from "svelte-gestures";

	import TableList from "./TableList.svelte";
	import NextPrev from "./NextPrev.svelte";
	import Log from "./Log.svelte";
	import {log} from "./Store.js";
	import {showDate} from "./DateStore";
    import { attr_dev } from "svelte/internal";
	import Bottom from "./Bottom.svelte";

	const version = "1.1";

	let logMessage = "";
	function clearLog(){
		$log = [];
	}

	const weekDays = [
        ["Sun","Sunday"],
        ["Mon","Monday"],
        ["Tue", "Tuesday"],
        ["Wed","Wednesday"],
        ["Thu","Thursday"],
        ["Fri","Friday"],
        ["Sat","Saturday"]
    ];
	const startingDate =  "2023-07-16";
	// I need a structure? or 3
	// date/s 
	// workers in order
	// shiftWeek
	var patternSelector = 1;
	// array of objects with information about week shift
	function createWeekPattern(patternSelector=0){
		return shiftsWeek[patternSelector].map((shift)=>shift.map((shiftDay)=>createDay(shiftDay)));
	}

	function createDay(shiftDay,selected=false){
		// let day =
		return  {
			class: shiftDay,
			shift: shiftKey[patternSelector][shiftDay],
			selected: selected
		};
	}

	function createSelectedDayPattern(day,weekPattern){
		const selectedDay = new Date(day);
		const dayNo = selectedDay.getDay();

		return weekPattern.map((shift)=>[shift[dayNo]]);
	}

	let weekPattern = createWeekPattern(patternSelector);


	// funciton calculates week based on given date and number of weeks
	function calcWeekNo(day,noOfWeeks){
        const start = new Date(startingDate);
		const day0 = new Date(day);
        let diff = day0.getTime() - start.getTime();
		// $log += diff;
        let days = diff/(1000*60*60*24);
		// $log += " days "+days;
		// console.log(days);
        let weeks = Math.floor(days/7);
		// $log += " weeks "+weeks;
		// console.log(weeks);
        let weekNo = weeks%noOfWeeks;
		// $log += " week no "+weekNo;
		
        return weekNo;
	}
	// object with table of workers that returns array 
	// for a given week
	let weekdWorkers = {
		workers: workers,
		Pattern(weekNo){
			return workers.slice(weekNo,workers.length).concat(workers.slice(0,weekNo));
		},
		CalculateWeek(date){
			let weekNo = this.workers.length-calcWeekNo(date,this.workers.length);
			return this.Pattern(weekNo);
		}
	}



	// dates
	function dateToString(date){
    	return  date.toISOString().slice(0,10);
	}

	let longDayName = true; // set to false for short name 
	
	function calculateWeekDates(day,longDayName){
		let week = [];
		// date and name of weekday
		let dayDate = new Date(day);
    	const dayOfWeek=dayDate.getDay();
		const longName = longDayName?1:0;

   		// calculate sunday using day0 and dayOfWeek
    	let sunday = new Date(day.concat("T10:00:00Z"));
		// for change of time to ot go over to previous day
    	sunday.setDate(sunday.getDate()-dayOfWeek);
		// addLog("Suday: "+sunday.getDay()+" : "+sunday.getDate());

		let dateForDay = new Date(dateToString(sunday));
		// $log += "date "+dateForDay;
		// $log += " day no: "+dateForDay.getDay()+ " <<>> ";
		for (let i=0; i<7;i++){
			if (dateForDay.getDay() == i){
				week[i] =[dateToString(dateForDay),weekDays[dateForDay.getDay()][longName]];
			}
			else {
				week[i] = ["ERROR","something went wrong"];
			}
        	dateForDay.setDate(dateForDay.getDate()+1);
		}
		return week;
	}
	
	function createSelectedDay(day,longDayName){
		const selectedDay = new Date(day);
		const longName = longDayName?1:0;
		// console.log(day);
		return [[day,weekDays[selectedDay.getDay()][longName]]];
	}
	
	function selectDayInWeekPattern(day){
		// change all selected to false
		// and for one day change selected to true
		weekPattern.forEach((shift)=>shift.forEach((day)=>day.selected=false));
		// adding selected first:
		const sDay = new Date(day);
		const dayNo = sDay.getDay(); // 0 -6
		
		//$log += weekPattern[1][dayNo].selected;
		weekPattern.forEach((shift)=>shift[dayNo].selected=true
		);
		// console.log(weekPattern);
	}

	const today = new Date();
    // let showDate = dateToString(today);
	$showDate = dateToString(today);

	$: {
			weekPattern = createWeekPattern(patternSelector);
			// createDay($showDate);

		};
	$: showWeek = calculateWeekDates($showDate,longDayName);
	$: selectedWeekWorkers = weekdWorkers.CalculateWeek($showDate);
	$: selectedDayPattern = createSelectedDayPattern($showDate,weekPattern);
	$: selectedDay = createSelectedDay($showDate,longDayName);
	$: {selectDayInWeekPattern($showDate);
		weekPattern = weekPattern;
		};

	// console.log("sel day "+selectedDay);
	let targetDate = "";

	// Swipes for next prev day
	function swipeDay(event){
		direction = event.detail.direction;
		alert("day");
		if (direction == "left"){
			showDate.nextDay();
		}
		else if (direction == "right"){
			showDate.prevDay();
		}	
	}
	function swipeWeek(event){
		direction = event.detail.direction;
		alert("week");
		if (direction == "left"){
			showDate.nextWeek();
		}
		else if (direction == "right"){
			showDate.prevWeek();
		}
	}
</script>

<main>
	<div id="tools" class="tools">
		<div id="datePanel">
			<label for="targetDay">Day </label>
			<input bind:value={$showDate} type="date" id="targetDay" name="targetDay" />
			<button on:click={()=>{patternSelector = (patternSelector==0)?1:0;}}>Switch Pattern</button>
			<button on:click={()=>{longDayName = (longDayName)?false:true;}}>Switch Day Name</button>
		</div>
		<div id="nextPrev">
			<NextPrev />
		</div>
	</div>
</main>
<div>
		<div id="dayPanel" style="flex:1" class="center" 
			use:swipe={{ timeframe: 300, minSwipeDistance: 30, touchAction: 'pan-x' }} on:swipe={swipeDay}>
			<TableList body={selectedDayPattern} dates={selectedDay} workers={selectedWeekWorkers} />
		</div>
		<div id="weekPanel" style="flex:4" class="center"
			use:swipe={{ timeframe: 300, minSwipeDistance: 100, touchAction: 'pan-x' }} on:swipe={swipeWeek}>

			<TableList body={weekPattern} dates={showWeek} workers={selectedWeekWorkers} />
		</div>

</div>
<Log bind:logMessage />
<!-- <button type="button" class="btn" on:click={() => changeIt("Somehing")}> change it  </button> -->
<!-- <TableList list={shiftsWeek} /> -->
<Bottom {version}/>
<style>
	main {
		text-align: center;
		/* padding: 1em; */
		padding: 0em;
		max-width: 240px;
		margin: 0 auto;
	}

	div {
		display: flex;	
		justify-content: center;
		align-items: center;
	}

	.center {
		text-align: center;
		display: flex;
		flex-flow: column;
		justify-content: center;
	}
	.tools {
		text-align: center;
		display: flex;
		flex-flow: column;
		justify-content: center;
		align-items: center;
	}
	/* label[for="targetDay"] {
		display: inline-block;
		vertical-align: middle;
	} */

	
	@media (min-width: 640px) {
		main {
			max-width: none;
		}
	}
	/* media query from mobile <480 growing */
	@media all and (max-width: 1200px) and (orientation: landscape)
	{
		#weekPanel {display: block;}
		#dayPanel {display: none;}
	}

	@media all and (max-width: 800px) and (orientation: landscape)
	{
		#weekPanel {display: block;font-size: 12px}
		#dayPanel {display: none;}
	}

	@media all and (orientation: portrait),(max-width: 576px)
	{
		#weekPanel {display: none;}
		#dayPanel {display: block;}
	}
</style>